import React, { useEffect, useState } from 'react';
import { Typography, List, Button, Message, Space } from '@arco-design/web-react';
import { IconDownload } from '@arco-design/web-react/icon';
import axios from 'axios';
import './file.css'; // CSS

const { Title, Text } = Typography;

const Filelist = () => {
  const [fileList, setFileList] = useState([]);

  useEffect(() => {
    // 
    axios.get('/file')
      .then(response => {
        setFileList(response.data.files);
      })
      .catch(error => {
        Message.error('');
        console.error(error);
      });
  }, []);

  const handleDownload = (filename) => {
    // 
    window.location.href = `/download?filename=${filename}`;
  };

  return (
    <div className="file-list-container">
      <div className="header-container">
        <Title heading={2} className="title"></Title>
        <Text type="secondary" className="description">
          
        </Text>
      </div>
      <Space direction="vertical" size="large" style={{ width: '100%' }}>
        <List
          dataSource={fileList}
          render={(item) => (
            <List.Item
              key={item}
              extra={
                <Button type="primary" icon={<IconDownload />} onClick={() => handleDownload(item)}></Button>
              }
            >
              <Typography.Text>{item}</Typography.Text>
            </List.Item>
          )}
        />
      </Space>
    </div>
  );
};

export default Filelist;
